import axios from "axios";
import React, { useCallback, useEffect, useRef, useState } from "react";
import Card from "./Card";
import SwapRacaToUSD from "./SwapRaca";
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Box, Typography, MenuItem, Select, FormControl } from '@mui/material';
import '../../styles/nfts.css';
import { useSelector } from "react-redux";
import millify from "millify";
import StatsChart from "./StatsChart/StatsChart";
import { useMemo } from "react";
import { selectIngameList, selectStatsList } from './constant'



const ConvertDDMM = (datetime) => {
    const [, time] = datetime.toLocaleString().split(',');
    return `${time.split(' ')[1]}`
}
const getAPI = (categoryId = '', size = '') => {
    return axios(`https://market-api.radiocaca.com/nft-sales`, {
        params: {
            pageSize: size,
            sortBy: 'fixed_price',
            order: 'asc',
            category: categoryId,
            tokenId: -1
        }

    });
}
const getStats = (categoryId = '') => {
    let tokenId;
    if (categoryId === 15 || categoryId === 16) {
        tokenId = 0
    } else {
        tokenId = -1
    }
    return axios(`https://market-api.radiocaca.com/nft-sales`, {
        params: {
            pageNo: 1,
            pageSize: 10,
            status: 'executed',
            tokenId: tokenId,
            category: categoryId
        }
    });
}
const getSellIngameList = (formDataPrams) => {
    const formDataLogin = new FormData();
    formDataLogin.append('address', "0x70a6DDd4468eBBd91d6EcA7643261b13D634bCa4");
    formDataLogin.append('sign', "0x0846f0452483b7704dc7395b1d9456943be927c939191f99b665c5d120d1de36713cfd387b8e78cee45430981edf096727efbc8fd96a76b4d48dca0e66b82b351c");
    formDataLogin.append('msg', "LogIn-d494e188-89ac-6d70-4d5d-af36602f8d3d");
    formDataLogin.append('network', "1");
    formDataLogin.append('clientType', "MetaMask");

    return axios({
        method: 'POST',
        url: 'https://metamon-api.radiocaca.com/usm-api/login',
        data: formDataLogin,
        headers: {
            "Content-Type": "multipart/form-data",
        }
    }).then((res) => {
        const data = res.data.data;
        return axios({
            method: 'POST',
            url: 'https://metamon-api.radiocaca.com/usm-api/shop-order/sellList',
            data: formDataPrams,
            headers: {
                "Content-Type": "multipart/form-data",
                "accesstoken": data.accessToken,
            }
        });
    }).catch(err => console.log(err))
}

export function numberWithCommas(x) {
    return x?.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}
const getFormData = (type) => {
    const formData = new FormData();
    formData.append('address', '0x70a6DDd4468eBBd91d6EcA7643261b13D634bCa4')
    formData.append('type', type)
    formData.append('orderType', 3)
    formData.append('orderId', -1)
    formData.append('pageSize', 5)
    return formData;
}





const NFTs = () => {
    const [selectStats, setSelectStats] = useState(selectStatsList[0].id);
    const [selectedStatsList, setSelectedStatsList] = useState([]);
    const [tokenPrice, setTokenPrice] = useState(0);
    const [timeUpdated, setTimeUpdated] = useState("")
    const [eggStatsList, setEggStatsList] = useState([]);
    const [sellIngameList, setSellIngameList] = useState([]);
    const [selectTypeIngame, setSelectTypeIngame] = useState(selectIngameList[0].type);

    const [listMetamon, setListMetamon] = useState([]);
    const [listDiamond, setListDiamond] = useState([]);
    const [listEgg, setListEgg] = useState([]);
    const [listPotion, setListPotion] = useState([]);
    const [kissUpLand, setKissUpLand] = useState({});
    const [BigGreen, setBigGreen] = useState({});
    const [MetamonR, setMetamonR] = useState({});
    const [MMLand, setMMLand] = useState({});
    const [listStats, setListStats] = useState([]);
    const [listStatsPotion, setListStatsPotion] = useState([]);

    const refSelect = useRef();

    const timeInterval = useMemo(() => 20000, [])

    const convertDateTime = () => {
        const now = new Date();
        setTimeUpdated(now.toLocaleString())
    }
    const handleChangeSelect = (e) => {
        // console.log(e.target.value)
        setSelectStats(e.target.value)
    }
    const handleChangeSelectSellIngame = (e) => {
        setSelectTypeIngame(e.target.value)
    }
    const { raca, elmon, elcoin, btc } = useSelector(state => state.price)
    const getData = useCallback(async () => {
        Promise.all([
            getAPI(13, 5).then(res =>
                setListMetamon(res.data.list)
            ),
            getAPI(15, 5).then(res =>
                setListPotion(res.data.list)
            ),
            getAPI(16, 5).then(res =>
                setListDiamond(res.data.list)
            ),
            getAPI(17, 5).then(res =>
                setListEgg(res.data.list)
            ),

            getStats(17).then(res =>
                setEggStatsList(res.data.list)
            ),

            getAPI(20, 1).then(res => {
                setKissUpLand(res.data.list[0]);
                document.querySelector('.loading')?.classList.toggle('loading');
            }),
            getAPI(23, 1).then(res => {
                setMetamonR(res.data.list[0]);
                document.querySelector('.loading')?.classList.toggle('loading');
            }),
            getAPI(7, 1).then(res => {
                setMMLand(res.data.list[0]);
                document.querySelector('.loading')?.classList.toggle('loading');
            }),
            getAPI(46, 1).then(res => {
                setBigGreen(res.data.list[0]);
                document.querySelector('.loading')?.classList.toggle('loading');
            }),
        ]);

    }, [])


    useEffect(() => {
        setTokenPrice(raca)
    }, [raca])

    useEffect(() => {
        getData();
        convertDateTime();
        const rerenderData = setInterval(() => {
            getData();
            convertDateTime();
        }, timeInterval)

        return () => {
            setEggStatsList([])
            setTokenPrice(0)
            clearInterval(rerenderData);
        }
    }, []);

    useEffect(() => {
        const getStatsNFT = () => {
            getStats(selectStats, 10).then(res => setSelectedStatsList(res.data.list));
        };
        getStatsNFT();
        const rerenderData = setInterval(() => {
            getStatsNFT();
        }, timeInterval)
        return () => {
            clearInterval(rerenderData)
        }
    }, [selectStats])


    useEffect(() => {
        const getChild = refSelect.current.childNodes[0]

        if (getChild?.childNodes[1]) {
            getChild?.removeChild(getChild?.childNodes[1])
        }

        getSellIngameList(getFormData(selectTypeIngame)).then(res => {
            if (res?.data?.data) {
                const data = res.data.data;
                setSellIngameList(data.shopOrderList)
            }

        }).catch(err => console.log(err));
        const rerenderStats = setInterval(() => {
            getSellIngameList(getFormData(selectTypeIngame)).then(res => {
                if (res?.data?.data) {
                    const data = res.data.data;
                    setSellIngameList(data.shopOrderList)
                }
            });
        }, 60000);
        return () => {
            clearInterval(rerenderStats)
            setSellIngameList([])
        }

    }, [selectTypeIngame])
    return (
        <Box>
            <Box sx={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
                <div className="statistical">
                    <Table className="priceToken" sx={{ color: '#fff' }}>
                        <TableHead>
                            <TableRow>
                                <TableCell colSpan={4}>Token Price</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            <TableRow>
                                <TableCell className='tokenPrice'>RACA</TableCell>
                                <TableCell className='tokenPrice'>ELMON</TableCell>
                                <TableCell className='tokenPrice'>ELCOIN</TableCell>
                                <TableCell className='tokenPrice'>BTC </TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>
                                    {raca}
                                </TableCell>
                                <TableCell>
                                    {elmon}
                                </TableCell>
                                <TableCell>
                                    {elcoin}
                                </TableCell>
                                <TableCell>
                                    {numberWithCommas(btc)}
                                </TableCell>
                            </TableRow>

                        </TableBody>

                    </Table>
                    <SwapRacaToUSD />
                    <p id='timeUpdated'>{timeUpdated}</p>
                    <div className='pricetable'>
                        <TableContainer className='table-scroll' component={Paper}>
                            <Table className='tablePrice' aria-label="simple table">
                                <TableHead>
                                    <TableRow>
                                        <TableCell align="center">#</TableCell>
                                        <TableCell align="center">
                                            <img width='50px' height='50px' src={`${process.env.PUBLIC_URL}/raca/metamon.png`} alt='Metamon' />
                                        </TableCell>
                                        <TableCell align="center">
                                            <img width='50px' height='50px' src={`${process.env.PUBLIC_URL}/raca/MetamonEgg.png`} alt='Egg' />
                                        </TableCell>
                                        <TableCell align="center">
                                            <img width='50px' height='50px' src={`${process.env.PUBLIC_URL}/raca/DiamondYellow.png`} alt='DiamondYellow' />
                                        </TableCell>
                                        <TableCell align="center">
                                            <img style={{ objectFit: 'contain' }} width='50px' height='50px' src={`${process.env.PUBLIC_URL}/raca/potion.png`} alt='Potion' />
                                        </TableCell>
                                        <TableCell align="center">
                                            <FormControl className='select-ingame'>
                                                <Select
                                                    size='small'
                                                    labelId="select-ingame"
                                                    id="select-ingame"
                                                    value={selectTypeIngame}
                                                    onChange={handleChangeSelectSellIngame}
                                                    sx={{ color: '#fff', padding: 0 }}
                                                    className='select-stats'
                                                    ref={refSelect}>
                                                    {selectIngameList && selectIngameList.map((item, index) => (
                                                        <MenuItem key={item.type} value={item.type}>
                                                            <img style={{ objectFit: 'contain' }} width='50px' height='50px' src={`${process.env.PUBLIC_URL}/raca/${item.img}`} alt={item.name} />
                                                            {item.name}
                                                        </MenuItem>
                                                    ))}
                                                </Select>
                                            </FormControl>
                                            {/* <img style={{ objectFit: 'contain' }} width='50px' height='50px' src='/valhalla.png' alt='valhalla' /> */}
                                        </TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {listMetamon.length !== 0 && [...Array(5).keys()].map((_, index) => (
                                        <TableRow key={index + 1}>
                                            <TableCell align="center">{index + 1}</TableCell>
                                            <TableCell align="center">{millify(Math.floor(listMetamon[index]?.fixed_price))} (~{(tokenPrice * listMetamon[index]?.fixed_price).toFixed(2)})</TableCell>
                                            <TableCell align="center">{numberWithCommas(Math.floor(listEgg[index]?.fixed_price))} (~{(tokenPrice * listEgg[index]?.fixed_price).toFixed(2)})</TableCell>
                                            <TableCell align="center">{numberWithCommas(Math.floor(listDiamond[index]?.fixed_price))} (~{(tokenPrice * listDiamond[index]?.fixed_price).toFixed(2)})</TableCell>
                                            <TableCell align="center">{numberWithCommas(Math.floor(listPotion[index]?.fixed_price))} (~{(tokenPrice * listPotion[index]?.fixed_price).toFixed(2)})</TableCell>
                                            <TableCell align="center">{numberWithCommas(Math.floor(sellIngameList[index]?.amount))} (~{(tokenPrice * sellIngameList[index]?.amount).toFixed(2)})</TableCell>
                                        </TableRow>
                                    )
                                    )}

                                </TableBody>
                            </Table>
                        </TableContainer>

                    </div>
                    <Box className="stats-session">
                        <Box sx={{ display: 'flex', justifyContent: 'space-between', padding: '5px 12px' }}>
                            <Typography style={{
                                textTransform: 'uppercase',
                                fontWeight: "bold",
                                fontSize: "larger",
                            }}>Stats</Typography>
                            <FormControl className='select-stats' style={{ width: '150px' }}>
                                <Select
                                    size='small'
                                    labelId="select"
                                    id="filter"
                                    value={selectStats}
                                    onChange={handleChangeSelect}
                                    sx={{ color: '#fff', }}
                                    className='select-stats'
                                >
                                    {selectStatsList && selectStatsList.map((item, index) => (
                                        <MenuItem key={item.id} value={item.id}>{item.name}</MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                        </Box>
                        <Box sx={{ display: 'flex', gap: "2%" }}>
                            <Box className='stats'>
                                <img width='50px' height='50px' className='symbols' src={`${process.env.PUBLIC_URL}/raca/MetamonEgg.png`} alt='Egg' />
                                <TableContainer className='table-scroll' component={Paper}>
                                    <Table className='tablePrice' aria-label="simple table">
                                        <TableHead>
                                            <TableRow>
                                                <TableCell align="center">#</TableCell>
                                                <TableCell align="center">
                                                    <p>Price</p>
                                                </TableCell>
                                                <TableCell align="center">
                                                    <p>Count</p>
                                                </TableCell>
                                                <TableCell align="center">
                                                    <p>Time</p>
                                                </TableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody>
                                            {eggStatsList?.length !== 0 && eggStatsList.map((item, index) => {
                                                return (
                                                    <TableRow key={index + 1}>
                                                        <TableCell align="center" sx={{ borderLeft: "1px solid" }}>{index + 1}</TableCell>
                                                        <TableCell style={{ paddingLeft: '5px' }} align="center">{numberWithCommas(Math.floor(item?.fixed_price / item.count))}</TableCell>
                                                        <TableCell align="center">{numberWithCommas(item.count)}</TableCell>
                                                        <TableCell align="center">{ConvertDDMM(new Date(item.timestamp * 1000))}</TableCell>
                                                    </TableRow>)
                                            })
                                            }
                                        </TableBody>
                                    </Table>
                                </TableContainer>
                            </Box>
                            <Box className='stats'>
                                <img width='50px' height='50px' style={{ objectFit: 'contain' }} className='symbols' src={`${process.env.PUBLIC_URL}/raca/${selectStatsList.filter(item => item.id === selectStats)[0].img}`} alt={selectStatsList.filter(item => item.id === selectStats)[0].name} />
                                <TableContainer className='table-scroll' component={Paper}>
                                    <Table className='tablePrice' aria-label="simple table">
                                        <TableHead>
                                            <TableRow>
                                                <TableCell align="center">#</TableCell>
                                                <TableCell align="center">
                                                    <p>Price</p>
                                                </TableCell>
                                                <TableCell align="center">
                                                    <p>{Number(selectStats) === 13 ? "Info" : "Count"}</p>
                                                </TableCell>
                                                <TableCell align="center">
                                                    <p>Time</p>
                                                </TableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody>
                                            {selectedStatsList?.length !== 0 && selectedStatsList?.map((item, index) => {
                                                return (
                                                    <TableRow key={index + 1}>
                                                        <TableCell align="center" sx={{ borderLeft: "1px solid" }}>{index + 1}</TableCell>
                                                        <TableCell align="center" style={{ paddingLeft: '1px' }}>{millify(Math.floor(item?.fixed_price / item.count))}</TableCell>
                                                        {Number(selectStats) === 13 ? (
                                                            <TableCell className="info-metamon-cell" padding='none'>
                                                                <Typography sx={{ color: '#fff', fontSize: '12px' }}>{item.level}</Typography>
                                                                <Typography sx={{ color: '#fff', fontSize: '12px' }}>{item.score}</Typography>
                                                            </TableCell>

                                                        ) : (
                                                            <TableCell align="center">{numberWithCommas(item.count)}</TableCell>

                                                        )}
                                                        <TableCell align="center">{ConvertDDMM(new Date(item.timestamp * 1000))}</TableCell>
                                                    </TableRow>)
                                            })
                                            }
                                        </TableBody>
                                    </Table>
                                </TableContainer>
                            </Box>
                        </Box>

                    </Box>


                </div>

            </Box>
            <Box className="cards">
                <Box className="card ">
                    <Card nft={MetamonR} />
                </Box>
                <Box className="card ">
                    <Card nft={BigGreen} />
                </Box>
                <Box className="card ">
                    <Card nft={MMLand} />
                </Box>
                <Box className="card ">
                    <Card nft={kissUpLand} />
                </Box>
            </Box>
            <StatsChart />
        </Box>

    );
}
export default NFTs;